import React , {useState} from "react";
import Typical from "react-typical";
import axios from "axios";
import { toast } from "react-toastify";

import ScreenHeading from "../../utilities/ScreenHeading/ScreenHeading";
import ScrollService from "../../utilities/ScrollService";
import Animations from "../../utilities/Animations";
import imgBack from "../../../src/images/mailz.jpeg";
import load1 from "../../../src/images/load2.gif";
import "./ContactMe.css";
import { window } from "rxjs";

export default function ContactMe(props) {
  let fadeInScreenHandler = (screen) => {
    if (screen.fadeInScreen !== props.id) return;
    Animations.animations.fadeInScreen(props.id);
  };

  const fadeInSubscription =
    ScrollService.currentScreenFadeIn.subscribe(fadeInScreenHandler);
    
  const [name , setName] = useState("")  
  const [email , setEmail] = useState("")  
  const [message , setMessage] = useState("")  
  const [banner , setBanner] = useState("")  
  const [boolean , setBoolean] = useState(false)  

  const handleName = (e) =>{
    setName(e.target.value);
  }
  const handleEmail = (e) =>{
    setEmail(e.target.value);
  }
  const handleMessage = (e) =>{
    setMessage(e.target.value);
  }

  const submitForm = async (e)=>{
    e.preventDefault();
    try{
      let data = {
        name,
        email,
        message,
      };
      setBoolean(true);
      const res = await axios.post('./contact',data);
      if(name.length === 0 || email.length === 0 || message.length === 0){
        setBanner(res.data.msg);
        toast.error(res.data.msg);
        setBoolean(false);
      }
      else if(res.status === 200){
        setBanner(res.data.msg);
        toast.success(res.data.msg);
        setBoolean(false);
        
        setName("")
        setEmail("")
        setMessage("")
      }
    }
    catch(error){
      console.log(error)
      
    }
    
  }

  // let screenFromDOM = document.getElementById("Contact Me");
  // const checkHeight = (elem)=>{
  //   let rec = elem.getBoundingClientRect();
  //   let elementBottomScreen = rec.bottom;
  //   if(elementBottomScreen >= window.innerHeight)
  //   return(true);
  //   else
  //   return(false);
    
  // }
  // console.log(checkHeight(screenFromDOM));


  return (
    <div className="main-container fade-in" id={props.id || ""}>
      <ScreenHeading title={"Contact Me"} subHeading={"Let's keep in touch"} />
      <div className="central-form ">
        <div className="col">
          <h2 className="title">
            {" "}
            <Typical
              loop={Infinity}
              steps={["Get in Touch", 1000, "Contact Me", 1000]}
            />
          </h2>
          <a
            href="https://www.facebook.com/profile.php?id=100008028372922"
            target="_blank"
          >
            <i className="fa fa-facebook-square"></i>
          </a>
          <a href="#">
            <i className="fa fa-google-plus-square"></i>
          </a>
          <a href="https://www.instagram.com/archismanbanik/" target="_blank">
            <i className="fa fa-instagram"></i>
          </a>
          <a
            href="https://www.youtube.com/channel/UCws3QQCG-3us-pOsA550Kng"
            target="_blank"
          >
            <i className="fa fa-youtube-square"></i>
          </a>
          <a
            href="https://www.linkedin.com/in/archisman-banik-b65913202/"
            target="_blank"
          >
            <i className="fa fa-linkedin"></i>
          </a>
        </div>
        <div className="back-form">
          <div className="img-back">
            <h4>Send Your Email Here</h4>
            <img src={imgBack} alt="image not found" />
          </div>
          <form action="" onSubmit={submitForm}>
            <p>{banner}</p>
            <label htmlFor="name">Name</label>
            <input type="text" 
            onChange={handleName}
            value={name}
            />

            <label htmlFor="email">Email</label>
            <input type="email" 
            onChange={handleEmail}
            value={email}
            />

            <label htmlFor="message">Message</label>
            <textarea type="text" 
            onChange={handleMessage}
            value={message}
            />

            <div className="send-btn">
              <button type="submit">
                send
                <i className="fa fa-paper-plane" />
                {boolean?<b className="load">
                  <img id="change-width" src={load1} alt="image not responding" />
                </b> : ("")}
              </button>
            </div>
          </form>
        </div>
        <div className="back-to-home">
          <button className="primary-btn"  
          type = "submit"
          id="go-home"
          onClick={() => ScrollService.scrollHandler.scrollToHome()}
          >
            <i className="fa fa-arrow-up"></i>
          </button>
        </div>
      </div>
    </div>
  );
}
